import { Product } from "../../../apiEndPoints";
import { logger } from "../../../utils";
import APIrequest from "../../axios";

export const productListServices = {
  getProductList: async (queryParams) => {
    try {
      const payload = {
        ...Product.allProduct,
        queryParams,
      };
      const res = await APIrequest(payload);
      return res;
    } catch (error) {
      logger(error);
      throw error;
    }
  },
  addProduct: async (bodyData) => {
    try {
      const payload = {
        ...Product.addProduct,
        bodyData,
      };
      const res = await APIrequest(payload);
      return res;
    } catch (error) {
      logger(error);
      throw error;
    }
  },
  updateProduct: async (id, bodyData) => {
    try {
      const payload = {
        ...Product.updateProduct(id),
        bodyData,
      };
      const res = await APIrequest(payload);
      return res;
    } catch (error) {
      logger(error);
      throw error;
    }
  },
  updateProductService: async (id, bodyData) => {
    try {
      const payload = {
        ...Product.deleteProduct(id),
        bodyData,
      };
      const res = await APIrequest(payload);
      return res;
    } catch (error) {
      logger(error);
      throw error;
    }
  },
};
