import { Formik, Form } from "formik";
import React from "react";
import {
  Input as TextInput,
  CommonButton,
  UploadInput,
  Select,
  AntTextArea,
} from "../../../components";
import { Media } from "../../../apiEndPoints";
import validation from "./validation";
import { enterOnlyNumericValue } from "../../../utils";

function AddProductForm(mainProps) {
  const { onSubmit, loading, formData, produtCategoryData, brandData } =
    mainProps;

  const initialValues = {
    image: formData?.image || "",
    name: formData?.name || "",
    brandId: formData?.brandId || undefined,
    price: formData?.price || "",
    categoryId: formData?.categoryId || undefined,
    description: formData?.description || "",
    packageName: formData?.packageName || "",
  };

  return (
    <Formik
      initialValues={{ ...initialValues }}
      validationSchema={validation()}
      onSubmit={onSubmit}
      enableReinitialize
    >
      {(props) => {
        return (
          <Form>
            <div className="form-group">
              <label className="form-label">Product Image</label>
              <div className="form-group">
                <div className="upload_photo mb-2 mb-md-3 mx-auto text-center">
                  <div className="img-box">
                    <UploadInput
                      name="image"
                      apiEndPoints={Media.profileImage}
                      type="file"
                      defaultImageUrl={formData?.image}
                      setFieldValue={props.handleChange}
                    />
                  </div>
                </div>
              </div>
            </div>
            <div className="form-group">
              <label className="form-label">Product Name</label>
              <div className="form-control-wrap">
                <TextInput
                  id="name"
                  className="form-control"
                  name="name"
                  disabled={false}
                  variant="standard"
                  type="text"
                  placeholder="Enter product name"
                  setFieldValue={props.handleChange}
                />
              </div>
            </div>
            <div className="form-group">
              <label className="form-label">Price</label>
              <div className="form-control-wrap">
                <TextInput
                  id="price"
                  className="form-control"
                  name="price"
                  disabled={false}
                  variant="standard"
                  type="text"
                  placeholder="Enter price"
                  setFieldValue={props.handleChange}
                  min="0"
                  inputmode="numeric"
                  onKeyPress={(e) => {
                    // onCheckValue(e);
                    enterOnlyNumericValue(e);
                  }}
                />
              </div>
            </div>
            <div className="form-group">
              <label className="form-label">Package Name</label>
              <div className="form-control-wrap">
                <TextInput
                  id="packageName"
                  className="form-control"
                  name="packageName"
                  disabled={false}
                  variant="standard"
                  type="text"
                  placeholder="Enter package name<"
                  setFieldValue={props.handleChange}
                />
              </div>
            </div>
            <div className="form-group">
              <div className="form-label-group">
                <label className="form-label" htmlFor="email">
                  Brand
                </label>
              </div>
              <div className="form-control-wrap">
                <Select
                  id="brandId"
                  name="brandId"
                  // disabled={false}s
                  variant="standard"
                  arrayOfData={brandData}
                  placeholder="Select Brand"
                  setFieldValue={props.handleChange}
                  // loanLoading={loanLoading}
                  //   onSelectChange={(e) => setCountryId(e)}
                />
              </div>
            </div>
            <div className="form-group">
              <div className="form-label-group">
                <label className="form-label" htmlFor="email">
                  Category
                </label>
              </div>
              <div className="form-control-wrap">
                <Select
                  id="categoryId"
                  name="categoryId"
                  variant="standard"
                  arrayOfData={produtCategoryData}
                  placeholder="Select Category"
                  setFieldValue={props.handleChange}
                />
              </div>
            </div>
            <div className="form-group">
              <div className="form-label-group">
                <label className="form-label" htmlFor="email">
                  Description
                </label>
              </div>
              <div className="form-control-wrap">
                <AntTextArea
                  id="description"
                  name="description"
                  variant="standard"
                  className="form-control"
                  placeholder="Enter description"
                  setFieldValue={props.handleChange}
                />
              </div>
            </div>

            <div className="form-group text-center">
              <CommonButton
                htmlType="submit"
                type="submit"
                loading={loading}
                extraClassName="btn btn-primary ripple-effect"
              >
                {formData?.id ? "Update Product" : "Add Product"}
              </CommonButton>
            </div>
          </Form>
        );
      }}
    </Formik>
  );
}

export default AddProductForm;
